import { createContext, useContext, useEffect, useState } from "react";
import { auth, db } from './firebase'
import { createUserWithEmailAndPassword, signInWithEmailAndPassword, onAuthStateChanged, signOut } from "firebase/auth";
import { setDoc, doc, getDoc, updateDoc, addDoc, collection, getDocs, onSnapshot } from "firebase/firestore";

const AuthContext = createContext({
    currentUser: null,
    currentUserData: { diamonds: '-' },
    mineshopData: [],
    register: () => Promise,
    login: () => Promise,
    logout: () => Promise,
    transfer: () => Promise,
    getUserData: () => Promise,
    createProduct: () => Promise,
    getAllProducts: () => Promise,
    getProduct: () => Promise,
    addReview: () => Promise,
    updateProduct: () => Promise,
    updateProfile: () => Promise,
    buyProduct: () => Promise,
    updateOrderStatus: () => Promise,
})

export const useAuth = () => useContext(AuthContext)

export default function AuthContextProvider({ children }) {
    const [currentUser, setCurrentUser] = useState(null)
    const [currentUserData, setCurrentUserData] = useState({ diamonds: '-' })
    const [mineshopData, setMineshopData] = useState(null)

    useEffect(() => {
        let unsubscribeUserData = () => { }
        let unsubscribeMineshop = () => { }

        const unsubscribe = onAuthStateChanged(auth, user => {
            setCurrentUser(user)
            if (user) {
                unsubscribeUserData = onSnapshot(doc(db, "users", user.uid), (doc) => {
                    // const source = doc.metadata.hasPendingWrites ? "Local" : "Server";
                    // console.log(source, " data: ", doc.data());

                    setCurrentUserData(doc.data())
                })
            }

            unsubscribeMineshop = onSnapshot(collection(db, "goods"), (querySnapshot) => {
                const goods = new Map()
                querySnapshot.forEach((doc, i) => {
                    let id = doc.id
                    doc = doc.data()
                    doc['id'] = id

                    goods.set(id, doc)
                })
                setMineshopData(goods)
            })
        })

        return () => {

            unsubscribe()
            unsubscribeUserData()
            unsubscribeMineshop()
        }
    }, [])

    async function createProduct(img, name, price, description, by, uid, goods) {
        let data = {
            img: img,
            name: name,
            price: price,
            description: description,
            by: by,
            uid: uid,
            reviews: []
        }
        const docRef = await addDoc(collection(db, "goods"), data)

        goods.push(docRef.id)

        await updateDoc(doc(db, "users", uid), {
            goods: goods
        })
    }

    async function updateProduct(id, img, name, price, description) {
        return await updateDoc(doc(db, "goods", id), {
            img: img,
            name: name,
            price: price,
            description: description
        })
    }

    async function buyProduct(productData, sellerUid, buyerData, buyerUid) {
        try {
            let price = productData['price']
            let date = new Date().getTime() + '#'

            if (parseInt(buyerData['diamonds']) - parseInt(price) < 0) {
                return ('Not enough money to buy ' + productData['name'] + '(' + (parseInt(price) - parseInt(buyerData['diamonds'])) + ' more needed)')
            }

            buyerData['diamonds'] = parseInt(buyerData['diamonds']) - parseInt(price)

            let sellerData = await getUserData(sellerUid)

            if (buyerData['nickname'] === sellerData['nickname']) {
                return 'You can`t buy your product'
            } else {
                buyerData['recieved'].push({
                    _key: date,
                    from: productData['by'],
                    price: price,
                    name: productData['name'],
                    img: productData['img'],
                    address: buyerData['address'],
                    done: false,
                    status: 0
                })

                let sellerRequseted = sellerData['requested']
                sellerRequseted.push({
                    buyer: buyerUid,
                    name: buyerData['nickname'],
                    product: {
                        price: price,
                        img: productData['img'],
                        address: buyerData['address'],
                        name: productData['name']
                    },
                    status: 0,
                    done: false,
                    _key: date
                })

                sellerData = {
                    diamonds: parseInt(sellerData['diamonds']) + parseInt(price),
                    requested: sellerRequseted
                }

                await updateUserData(sellerUid, sellerData)
                await updateUserData(buyerUid, buyerData)

                return 'ok'
            }
        } catch (error) {
            return error
        }
    }

    async function updateOrderStatus(productData, sellerData, status, done, id, sellerUid) {
        const buyerData = (await getDoc(doc(db, "users", productData['buyer']))).data()

        console.log(id)
        let ok = true
        buyerData['recieved'].forEach((product, i) => {
            if (product['_key'] === productData['_key']) {
                buyerData['recieved'][i]['status'] = status
                if (buyerData['recieved'][i]['done'] === true) {
                    ok = false
                }
                buyerData['recieved'][i]['done'] = done
            }
        })

        if (!ok) {
            return 'Status already updated'
        } else {
            sellerData['requested'][id]['status'] = status
            sellerData['requested'][id]['done'] = done

            if (status === -1) {
                sellerData['diamonds'] = parseInt(sellerData['diamonds']) - parseInt(productData['product']['price'])
                buyerData['diamonds'] = parseInt(buyerData['diamonds']) + parseInt(productData['product']['price'])
            }

            await updateUserData(sellerUid, sellerData)
            await updateUserData(productData['buyer'], buyerData)
            return 'Updated'
        }
    }

    async function getAllProducts() {
        return await getDocs(collection(db, "goods"));
    }

    async function getProduct(id) {
        return await getDoc(doc(db, "goods", id));
    }

    async function addReview(id, by, rating, description) {
        const docSnap = await getDoc(doc(db, "goods", id))
        const data = docSnap.data()
        let reviews = docSnap.data()['reviews']

        if (reviews === undefined)
            reviews = []

        reviews.push({
            by: by,
            rating: rating,
            description: description
        })

        await updateDoc(doc(db, "goods", id), {
            reviews: reviews
        });
    }

    async function register(email, password, nickname) {
        const docRef = doc(db, "userRefs", nickname);
        const docSnap = await getDoc(docRef);
        if (!(docSnap.data() === undefined))
            return 'user-error'

        return await createUserWithEmailAndPassword(auth, email, password).then(userObj => {
            try {
                setDoc(doc(db, "users", userObj.user.uid), {
                    diamonds: 0,
                    nickname: nickname,
                    img: '',
                    address: '',
                    requested: [],
                    recieved: [],
                    transactions: [{
                        img: '',
                        who: 'Minebank',
                        amount: '-',
                        description: 'Welcome to Minebank!'
                    }],
                    goods: []
                })
                setDoc(doc(db, "userRefs", nickname), {
                    uid: userObj.user.uid
                })
            } catch (e) {
                console.error("Error adding document: ", e)
            }
        })
    }

    async function login(email, password) {
        return signInWithEmailAndPassword(auth, email, password)
    }

    async function getUserData(uid) {
        const docRef = doc(db, "users", uid);
        const docSnap = await getDoc(docRef);

        if (docSnap.exists()) {
            return docSnap.data()
        } else {
            console.log("No such document!");
            return
        }
    }

    async function transfer(recieverNickname, amount, desc, from) {
        const docSnap = await getDoc(doc(db, "userRefs", recieverNickname))

        if (docSnap.exists()) {
            const recUid = docSnap.data()["uid"]
            const currentUid = currentUser.uid
            let currentUserBal = await getDoc(doc(db, "users", currentUid))
            let currentTransactions = currentUserBal.data()["transactions"]
            let senderImg = currentUserBal.data()['img']

            currentUserBal = currentUserBal.data()["diamonds"]

            let recBal = await getDoc(doc(db, "users", recUid))
            let recTransactions = recBal.data()["transactions"]
            let recieverImg = recBal.data()['img']

            recBal = recBal.data()["diamonds"]

            if (currentUserBal - amount >= 0) {
                currentTransactions.push({
                    amount: `-${amount}`,
                    who: `${recieverNickname}`,
                    description: `${desc}`,
                    img: recieverImg
                })
                await updateDoc(doc(db, "users", currentUid), {
                    diamonds: currentUserBal - amount,
                    transactions: currentTransactions
                })

                recTransactions.push({
                    amount: `+${amount}`,
                    who: `${from}`,
                    description: `${desc}`,
                    img: senderImg
                })
                await updateDoc(doc(db, "users", recUid), {
                    diamonds: parseFloat(recBal) + parseFloat(amount),
                    transactions: recTransactions
                })
                return 'success'
            }
            return 'You don`t have enough money for this'
        } else {
            return `User "${recieverNickname}" doesn\`t exist`
        }
    }

    async function updateProfile(uid, img, address) {
        await updateDoc(doc(db, "users", uid), {
            img: img,
            address: address
        })
    }

    async function updateUserData(uid, data) {
        await updateDoc(doc(db, "users", uid), data)
    }

    function logout() {
        return signOut(auth)
    }

    const value = {
        currentUser,
        currentUserData,
        mineshopData,
        register,
        login,
        logout,
        getUserData,
        transfer,
        createProduct,
        getAllProducts,
        getProduct,
        addReview,
        updateProduct,
        updateProfile,
        buyProduct,
        updateOrderStatus,
    }

    return <AuthContext.Provider value={value}>
        {/* {JSON.stringify(currentUserData)} */}
        {children}
    </AuthContext.Provider>
}
