import React, { Fragment, useEffect, useState } from 'react'
import Footer from '../footer/Footer'
import Topbar from './Topbar'
import { Card, Partition } from './User'
import { useAuth } from '../../firebase/AuthContext'
import UserNav from './UserNav'
import './Calculator.css'
import { NoData } from './Mineshop'

export default function Calculators() {
    const { currentUser, getUserData, listenUserData } = useAuth()
    const [userData, setUserData] = useState({ diamonds: '-' })

    const [items, setItems] = useState(0)

    useEffect(() => {
        if (currentUser) {
            getUserData(currentUser.uid)
                .then(response => {
                    setUserData(response)
                })
        }
    }, []);

    let itemRes = []

    let num = items
    if (num >= 1728) {
        itemRes.push(<Fragment><span className="big">{parseInt(num / 1728)}</span><span className="small">{' shulcker' + (parseInt(num / 1728) <= 1 ? '' : 's') + ', '}</span></Fragment>)

        num -= 1728 * parseInt(num / 1728)
    }

    if (num >= 64) {
        itemRes.push(<Fragment><span className="big">{parseInt(num / 64)}</span><span className="small">{' stack' + (parseInt(num / 64) <= 1 ? '' : 's') + ', '}</span></Fragment>)
    }

    itemRes.push(<Fragment><span className="big">{num % 64}</span><span className="small">{' item' + (num % 64 <= 1 ? '' : 's')}</span></Fragment>)

    return (
        <div>
            <Topbar />
            <div className='main-container'>
                <Partition img={userData['img']} text={`${userData['nickname'] ? 'Hello, ' + userData['nickname'] + '!' : 'Loading data'}`} />
                <Card amount={userData['diamonds']} />
                <UserNav />

                <Partition text='Item' />
                <input className="input" type="number" onChange={e => setItems(e.target.value)} min="0" max={Number.MAX_SAFE_INTEGER} />

                <p className="result">{itemRes}</p>

                <Partition text='Distance' />
            </div>
            <Footer />
        </div>
    )
}
