import React, { useEffect, useState } from 'react'
import { Navigate } from 'react-router-dom'
import { Fragment } from 'react/cjs/react.production.min'
import { useAuth } from '../../firebase/AuthContext'
import Footer from '../footer/Footer'
import { NoData } from './Mineshop'
import Topbar from './Topbar'
import { Card, Partition } from './User'
import UserNav from './UserNav'
import { useSnackbar } from 'notistack'

export default function Profile() {
    const { currentUser, currentUserData } = useAuth()

    return (
        <div>
            {!currentUser ? <Navigate to='/login' state={{ from: '/profile' }} /> : ''}
            <Topbar />
            <div className='main-container'>
                <Partition img={currentUserData['img']} text={`${currentUserData['nickname'] ? 'Hello, ' + currentUserData['nickname'] + '!' : 'Loading data'}`} />

                <Card amount={currentUserData['diamonds']} />

                <UserNav />

                <Partition text={'Profile info'} />

                <Settings img={currentUserData['img'] + 'a'} address={currentUserData['address']} uid={currentUser?.uid} />

                {/* <Partition text={'Statistics'} />
                <Statistics /> */}
            </div>
            <Footer />
        </div>
    )
}

function Settings(props) {
    const { enqueueSnackbar } = useSnackbar()
    const { updateProfile } = useAuth()

    const [img, setImg] = useState(props.img)
    const [address, setAddress] = useState(props.address)

    return (
        <Fragment>
            <p className="tc-name">Profile image url:</p>
            <input className='input' value={img} onChange={(e) => { setImg(e.target.value) }} />
            <p className="tc-name">Delivery adress:</p>
            <input className='input' value={address} onChange={(e) => { setAddress(e.target.value) }} />
            <button className='button' onClick={(e) => {
                e.preventDefault()

                updateProfile(props.uid, img, address)
                    .then(() => {
                        enqueueSnackbar('Profile updated', { variant: 'success' })
                    })
            }}>Save</button>
        </Fragment>
    )
}

function Statistics() {

    return (
        <Fragment>
            <p className="tc-name">Times transfered: 10</p>
            <p className="tc-name">Products created: 4</p>
            <p className="tc-name">Products ordered: 8</p>
            <p className="tc-name">Products sold: 5</p>
        </Fragment>
    )
}